:root {
  --color: #00acdc;
}

* {
  box-sizing: border-box;
}

html {
  margin: 0;
  padding: 0;
  font-size: 62.5%;
}

body {
  font-family: Roboto, sans-serif;
  font-size: 1.2rem;
  line-height: 1.2;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 50px;
}

.header-container {
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo {
  flex: none;
  justify-content: flex-start;
  align-self: center;
  width: 40px;
  height: 40px;
}

.nav {
  flex: 0 auto;
}

.nav ul {
  text-transform: uppercase;
  flex-wrap: nowrap;
  justify-content: center;
  margin: 0;
  padding-inline-start: 0;
  list-style: none;
  display: flex;
}

.nav_link {
  text-align: center;
  text-wrap-mode: nowrap;
  color: #000;
  padding: 23px 10px;
  font-weight: 500;
  text-decoration: none;
  display: inline-block;
}

.nav_link.is-active {
  color: var(--color);
  font-weight: normal;
  position: relative;
}

.nav_link.is-active:after {
  content: "";
  background-color: var(--color);
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  width: 37px;
  height: 4px;
  display: flex;
  position: absolute;
  bottom: 0;
}

.nav_link:hover {
  color: var(--color);
}
